html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--FONT-INTER);
  background-color: var(--BACKGROUND);
  color: var(--TEXT-COLOUR);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1, h2, h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --LIMEGREEN: hsl(163, 72%, 41%);
  --BRIGHTRED: hsl(356, 69%, 56%);
  --FACEBOOK-COLOUR: hsl(208, 92%, 53%);
  --TWITTER-COLOUR: hsl(203, 89%, 53%);
  --INSTAGRAM-START-COLOUR: hsl(37, 97%, 70%);
  --INSTAGRAM-END-COLOUR: hsl(329, 70%, 58%);
  --YOUTUBE-COLOUR: hsl(348, 97%, 39%);
  --TOGGLE-BG-START: hsl(210, 78%, 56%);
  --TOGGLE-BG-END: hsl(146, 68%, 55%);
  --TOGGLE-LIGHT: hsl(230, 19%, 60%);
  --TOGGLE-BG-LIGHT: hsl(230, 22%, 74%);
  --TOGGLE-BUTTON-LIGHT: hsl(228, 46%, 96%);
  --BACKGROUND: var(--LIGHT-BG);
  --TEXT-COLOUR: var(--LIGHT-TEXT2);
  --TEXT-COLOUR2: var(--LIGHT-TEXT1);
  --CARD-BG: var(--LIGHT-CARD-BG);
  --CARD-HOVER: var(--LIGHT-CARD-HOVER);
  --TOGGLE: var(--TOGGLE-LIGHT);
  --TOGGLE-BG: var(--TOGGLE-BG-LIGHT);
  --TOGGLE-BUTTON: var(--TOGGLE-BUTTON-LIGHT);
  --DARK-BG: hsl(230, 17%, 14%);
  --DARK-TOP-BG:hsl(232, 19%, 15%);
  --DARK-CARD-BG: hsl(228, 28%, 20%);
  --DARK-TEXT1: hsl(228, 34%, 66%);
  --DARK-TEXT2: hsl(0, 0%, 100%);
  --DARK-CARD-HOVER: hsl(228, 25%, 27%);
  --LIGHT-BG: hsl(0, 0%, 100%);
  --LIGHT-TOP-BG: hsl(225, 100%, 98%);
  --LIGHT-CARD-BG: hsl(227, 47%, 96%);
  --LIGHT-TEXT1: hsl(228, 12%, 44%);
  --LIGHT-TEXT2: hsl(230, 17%, 14%);
  --LIGHT-CARD-HOVER: hsl(228, 33%, 91%);
}

@media (prefers-color-scheme: dark) {
  :root {
    --BACKGROUND: var(--DARK-BG);
    --TEXT-COLOUR: var(--DARK-TEXT2);
    --TEXT-COLOUR2: var(--DARK-TEXT1);
    --CARD-BG: var(--DARK-CARD-BG);
    --CARD-HOVER: var(--DARK-CARD-HOVER);
    --TOGGLE: var(--LIGHT-BG);
    --TOGGLE-BG: linear-gradient(225deg, var(--TOGGLE-BG-END) 0%, var(--TOGGLE-BG-START) 98.02%);
    --TOGGLE-BUTTON: var(--DARK-BG);
  }
}
body.dark {
  --BACKGROUND: var(--DARK-BG);
  --TEXT-COLOUR: var(--DARK-TEXT2);
  --TEXT-COLOUR2: var(--DARK-TEXT1);
  --CARD-BG: var(--DARK-CARD-BG);
  --CARD-HOVER: var(--DARK-CARD-HOVER);
  --TOGGLE: var(--LIGHT-BG);
  --TOGGLE-BG: linear-gradient(225deg, var(--TOGGLE-BG-END) 0%, var(--TOGGLE-BG-START) 98.02%);
  --TOGGLE-BUTTON: var(--DARK-BG);
}

body.light {
  --BACKGROUND: var(--LIGHT-BG);
  --TEXT-COLOUR: var(--LIGHT-TEXT2);
  --TEXT-COLOUR2: var(--LIGHT-TEXT1);
  --CARD-BG: var(--LIGHT-CARD-BG);
  --CARD-HOVER: var(--LIGHT-CARD-HOVER);
  --TOGGLE: var(--TOGGLE-LIGHT);
  --TOGGLE-BG: var(--TOGGLE-BG-LIGHT);
  --TOGGLE-BUTTON: var(--TOGGLE-BUTTON-LIGHT);
}

:root {
  --FONT-INTER: "Inter", sans-serif;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background: var(--CARD-BG);
  color: var(--TEXT-COLOUR2);
  flex: 1;
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  position: relative;
  overflow: hidden;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--CARD-HOVER);
}
.card--facebook {
  border-top: 0.3125rem solid var(--FACEBOOK-COLOUR);
}
.card--twitter {
  border-top: 0.3125rem solid var(--TWITTER-COLOUR);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  width: 100%;
  height: 0.3125rem;
  width: 100%;
  background: linear-gradient(225deg, var(--INSTAGRAM-END-COLOUR), var(--INSTAGRAM-START-COLOUR) 100%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--YOUTUBE-COLOUR);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1.25rem;
  margin-bottom: 1.75rem;
  margin-top: 0.3125rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--TEXT-COLOUR);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  color: var(--TEXT-COLOUR2);
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
  margin-bottom: 0.5625rem;
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card__change--up {
  color: var(--LIMEGREEN);
}
.card__change--down {
  color: var(--BRIGHTRED);
}
.card__change img {
  margin-right: 0.25rem;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  margin: 0;
}
.toggle__wrapper {
  height: 1.5rem;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -0.125rem 0 -0.125rem;
  height: 1.5rem;
  width: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  position: absolute;
  height: 100%;
  width: 100%;
  border-radius: 0.75rem;
  overflow: hidden;
  background: var(--TOGGLE-BG);
  top: 0;
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--TOGGLE-BUTTON);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  right: 3px;
  left: calc(100% - 21px);
}
.toggle #system:checked ~ .toggle__button {
  right: auto;
  left: 50%;
  transform: translate(-50%);
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--TOGGLE-DARK);
}
.toggle label[for=dark] {
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--TEXT-COLOUR2);
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: end;
}
.card-grid .img,
.card-grid .card__change {
  justify-self: end;
}